import React, { Component } from "react";

class NombreFoto extends Component {
  render() {
    const { nombre, photo } = this.props;
    return (
      <div className="text-center my-0" style={{ color: "#ffffff" }}>

        <img
          src={photo}
          alt={"profile_photo"}
          width="35px"
          style={{ borderRadius: "50%", marginRight: "10px",marginLeft: "10px" }}

        />
        <span className="mr-3" style={{fontSize:"1.3em"}}>{nombre.substr(0,nombre.indexOf(' '))}</span>

      </div>
    );
  }
}

export default NombreFoto;
